.continue__tologin{
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    background: #ececec;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    /* z-index: 9; */
}

.continue__tologin > img{
    width: 10rem;
    object-fit: contain;
}
h1,h3,h5{
    font-family: 'Fredoka One', cursive;
}

h5{
    margin-top: 1.5rem;
}
h5:hover{
    cursor: pointer;
}